<template>
  <div>
    <div class="headerZINDEX">
      <van-nav-bar
      :fixed="true"
        title="任务管理"
        @click-left="()=>{
          $router.go(-1)
         }"
        left-arrow
        class="backgroundNaviBar"
        z-index="999"
        :border="false"
      >
        <!-- <van-icon name="search" slot="right" /> -->
      </van-nav-bar>
    </div>
    <div class="positionImageSize" :style="position" v-show="status">
      <div>
        <mu-row class="flexCenterMain">
          <mu-col span="3" class="flexCenterMainChild">任务类型：</mu-col>
          <mu-col span="9" lg="4" sm="6">
            <mu-select label v-model="normal.value1" full-width>
              <mu-option
                v-for="(option,index) in options"
                :key="index"
                :label="option"
                :value="option"
              ></mu-option>
            </mu-select>
          </mu-col>
        </mu-row>
        <mu-row class="flexCenterMain">
          <mu-col span="3">接收人员：</mu-col>
          <mu-col span="9"></mu-col>
        </mu-row>
        <div style class="submitStyles" @click="submitBtn()">
          <span>提交</span>
        </div>
      </div>
    </div>
    <mescroll-vue ref="mescroll" :down="mescrollDown" :up="mescrollUp" @init="mescrollInit">
      <div class="containerListContent border-radius">
        <div
          class="contentList"
          v-for="(item,index) in list"
          :key="index"
          @click="clickListBtn(item.taskId)"
        >
          <div class="listNameTitle">
            <span style="color:#7a7a7a">任务类型:</span>
            <span style="font-size:14px;" class="colorSize">{{item.type}}</span>
          </div>
          <div class="listNameTitle">
            <span style="color:#7a7a7a">接收人员:</span>
            <span style="font-size:14px;" class="colorSize">{{item.recevPerson}}</span>
          </div>
          <div class="listNameTitle">
            <span style="color:#ccc">{{item.gmtCreate}}</span>
          </div>
        </div>
      </div>
    </mescroll-vue>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import MescrollVue from "mescroll.js/mescroll.vue";
export default {
  name: "eventControlList",
  data() {
    return {
      list: [],
      options: ["沙井一期进水口水质监测站"],
      value3: null,
      normal: {
        value1: null,
        value2: null
      },
      position: "",
      status: false,
      mescroll: null, // mescroll实例对象
      mescrollDown: {}, //下拉刷新的配置. (如果下拉刷新和上拉加载处理的逻辑是一样的,则mescrollDown可不用写了)
      mescrollUp: {
        // 上拉加载的配置.
        callback: this.initdata, // 上拉回调,此处简写; 相当于 callback: function(page, mescroll) { }
        //以下是一些常用的配置,当然不写也可以的.
        page: {
          num: 0, //当前页 默认0,回调之前会加1; 即callback(page)会从1开始
          size: 20 //每页数据条数,默认10
        },
        htmlNodata: '<p class="upwarp-nodata">-- END --</p>',
        noMoreSize: 5, //如果列表已无数据,可设置列表的总数量要大于5才显示无更多数据;
        // 避免列表数据过少(比如只有一条数据),显示无更多数据会不好看
        // 这就是为什么无更多数据有时候不显示的原因
        toTop: {
          //回到顶部按钮
          src: "./static/mescroll/mescroll-totop.png", //图片路径,默认null,支持网络图
          offset: 1000 //列表滚动1000px才显示回到顶部按钮
        },
        empty: {
          //列表第一页无任何数据时,显示的空提示布局; 需配置warpId才显示
          warpId: "xxid", //父布局的id (1.3.5版本支持传入dom元素)
          icon: "./static/mescroll/mescroll-empty.png", //图标,默认null,支持网络图
          tip: "暂无相关数据~" //提示
        }
      }
    };
  },
  beforeRouteEnter(to, from, next) {
    // 如果没有配置回到顶部按钮或isBounce,则beforeRouteEnter不用写
    next(vm => {
      // 找到当前mescroll的ref,调用子组件mescroll-vue的beforeRouteEnter方法
      vm.$refs.mescroll && vm.$refs.mescroll.beforeRouteEnter(); // 进入路由时,滚动到原来的列表位置,恢复回到顶部按钮和isBounce的配置
    });
  },
  beforeRouteLeave(to, from, next) {
    // 如果没有配置回到顶部按钮或isBounce,则beforeRouteLeave不用写
    // 找到当前mescroll的ref,调用子组件mescroll-vue的beforeRouteLeave方法
    this.$refs.mescroll && this.$refs.mescroll.beforeRouteLeave(); // 退出路由时,记录列表滚动的位置,隐藏回到顶部按钮和isBounce的配置
    next();
  },
  methods: {
    mescrollInit(mescroll) {
      this.mescroll = mescroll; // 如果this.mescroll对象没有使用到,则mescrollInit可以不用配置
    },
    fullscreen() {
      this.loading = this.$loading();
    },
    clickListBtn(e) {
      this.$router.push({
        path: "/TaskControlListDetails",
        query: { taskId: e }
      });
    },
    imgShowClose() {
      //   this.position = "position:absolute;top:400px;";
      this.status = true;
    },
    submitBtn() {
      this.status = false;
    },
    async initdata(page, mescroll) {
      this.fullscreen();
      var url = "/task";
      var pageNum = page.num;
      var pageSize = page.size;
      var type = "";
      var res = await AppBace.request(
        url,
        "GET",
        {
          pageNum,
          pageSize
        },
        this.token
      );
      console.log(res);

      if (res.status === 200) {
        this.loading.close();
        let arr = res.data.data.rows;
        // 如果是第一页需手动置空列表
        if (page.num === 1) this.list = [];
        // 把请求到的数据添加到列表
        this.list = this.list.concat(arr);
        // 数据渲染成功后,隐藏下拉刷新的状态
        this.$nextTick(() => {
          mescroll.endSuccess(arr.length);
        });
      } else {
        this.loading.close();
      }
      // this.list = res.data.data.rows;
      for (let index = 0; index < this.list.length; index++) {
        const element = this.list[index].gmtCreate;
        var inspact = this.list[index].type;
        switch (inspact) {
          case "inspect":
            this.list[index].type = "例行巡查";
            break;
          case "repair":
            this.list[index].type = "故障维修";
            break;
          case "maintenance":
            this.list[index].type = "保养检修";
            break;
        }
        this.list[index].gmtCreate = AppBace.filterTime(element);
      }
    }
  },
  mounted() {
    this.token = sessionStorage.getItem("token");
    // this.initdata();
  },
  components: {
    MescrollVue
  }
};
</script>
<style scoped>
.listNameTitle {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 25px;
}
.containerListContent {
  /* width: 100%; */
  margin: 0 10px;
  /* padding: 10px 0; */
  background: white;
  /* border-radius: 10px; */
  margin-top: 56px;
}
.contentList {
  border-bottom: 1px solid rgb(243, 241, 241);
  padding: 10px 0;
}
.positionImageSize {
  width: 100%;
  text-align: center;
  background: white;
  position: absolute;
  top: 60px;
  padding: 0 10px;
  padding-bottom: 10px;
}
.imgPosition {
  /* margin-bottom:-20px; */
  /* position: absolute;
    bottom: 0; */
  width: 100%;
  text-align: center;
  position: relative;
  top: -7px;
}
.imgPosition img {
  width: 60px;
}
.flexCenterMain {
  display: flex;
  align-content: center;
  height: 50px;
}
.headerZINDEX {
  width: 100%;
  z-index: 999;
  position: absolute;
  top: 0;
}
.submitStyles {
  width: 100%;
  background: #4ed995;
  height: 40px;
  display: flex;
  color: white;
  font-size: 16px;
  /* align-content: center; */
  justify-content: center;
  align-items: center;
}
.colorSize {
  color: rgb(131, 130, 130);
}
/* .mescroll {
  position: fixed;
  top: 44px;
  bottom: 0;
  height: auto;
} */
</style>



